export default{
	data(){
		return{
			height:'',
		}
	},
	onReady(){
			this.getTopheight();
	},
	methods:{
			getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
				}
			})
		},
	}
}
